import './MobileOrderView.css'

import { useObserver } from 'mobx-react-lite'
import React from 'react'

import { useEmptyOrderRedirect } from '../../hooks/useEmptyOrderRedirect'
import { useOrderModel } from '../../hooks/useOrderModel'
import { CartView } from '../cart/CartView'
import { FoodMenuView } from '../foodMenu/FoodMenuView'
import { MobileClosingTimeModal } from './MobileClosingTimeModal'

export const MobileOrderView = () => {
  const orderModel = useOrderModel()
  useEmptyOrderRedirect()
  return useObserver(() => (
    <div className="mobile__order__view">
      <FoodMenuView />
      <CartView />
      <MobileClosingTimeModal branchId={orderModel.branchId!} />
    </div>
  ))
}
