import { observer } from 'mobx-react-lite'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { useQMobileShopIntro } from '../../graph/generated'

export const MobileShopIntro = observer(() => {
  const { business } = useRoot()
  const { data, loading } = useQMobileShopIntro(business.brandOrBranch, {
    skip: !business.isValid,
  })

  return loading ? null : (
    <div data-testid="MobileShopIntro">{data.intro.headerText}</div>
  )
})
