import React from 'react'
import styled from 'styled-components/macro'

import { PrimaryButton, SecondaryButton } from '../../common/Buttons'
import { FPickupBranchFragment } from '../../graph/generated'
import { i18n } from '../../i18n'
import { MobilePickupBranch } from './MobilePickupBranch'

type TBranch = FPickupBranchFragment

interface IProps {
  branches: ReadonlyArray<TBranch>
  onSelect(site: TBranch): void
  onShowMap(site: TBranch): void
}

const StyledMobilePickupList = styled.div`
  .buttons {
    text-align: right;

    & button {
      font-size: 0.9rem;
      padding: 0.3rem 0.6rem;
      margin: 0.5rem 0 0 0.4rem;
    }

    .flaticon-mark::before,
    .flaticon-signs::before {
      margin: 0 0.6rem 0 0;
      font-size: 0.9rem;
      font-weight: initial;
    }
  }
`

const MobilePickupList: React.FC<IProps> = ({
  branches,
  onSelect,
  onShowMap,
}) => {
  const renderBranch = (branch: TBranch) => {
    return (
      <MobilePickupBranch
        branch={branch}
        data-testid="MobilePickupListBranch"
        key={branch.id}
      >
        <div className="buttons">
          <SecondaryButton onClick={() => onShowMap(branch)}>
            <span className="flaticon-signs">{i18n.t`Show on map`}</span>
          </SecondaryButton>
          {branch.pickupEnabled ? (
            <PrimaryButton onClick={() => onSelect(branch)}>
              <span className="flaticon-mark">{i18n.t`Choose`}</span>
            </PrimaryButton>
          ) : (
            <PrimaryButton disabled={true}>
              <span className="flaticon-mark">{i18n.t`Delivery only`}</span>
            </PrimaryButton>
          )}
        </div>
      </MobilePickupBranch>
    )
  }

  return (
    <StyledMobilePickupList data-testid="MobilePickupList">
      {branches && branches.map(renderBranch)}
    </StyledMobilePickupList>
  )
}

export default MobilePickupList
