import { useRouterContext } from '@speedlo/hooks'
import React from 'react'
import styled from 'styled-components/macro'

import BackgroundThemed from '../../common/BackgroundThemed'
import { ColStyle } from '../../common/ColStyle'
import { LanguagePicker } from '../../common/LanguagePicker'
import { OrderMethod } from '../map/destination/OrderMethod'
import { ShopHeaderIcons } from '../shop/ShopHeaderIcons'
import { MobileShopIntro } from './MobileShopIntro'

const StyledMobileIntroView = styled.div`
  text-align: left;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  background-color: #e9e9e9;

  .mobile__intro__logo {
    position: relative;
    flex-grow: 2;
    background-size: cover;
    -moz-background-size: cover;
    -webkit-background-size: cover;
    -o-background-size: cover;
    width: 100%;
    margin: 0;

    & > div:first-of-type {
      margin-top: 0.5rem;
    }
  }
  .mobile__intro__info {
    flex-basis: auto;
    text-align: center;
    padding: 1rem 1rem;
    -webkit-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 9);
    -moz-box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.9);
    box-shadow: 0px 0px 10px 0px rgba(0, 0, 0.9);
    position: relative;
    z-index: 10;
  }

  .mobile__intro__info p {
    color: #5a5a5a;
  }

  .mobile_languagePicker {
    position: absolute;
    right: 0.3rem;
    margin-top: 0.5rem;
  }
`

export const MobileIntroView: React.FC = () => {
  const { history } = useRouterContext()
  return (
    <StyledMobileIntroView>
      <BackgroundThemed
        className="mobile__intro__logo"
        data-testid="MobileIntroView__BackgroundThemed"
        imageName="storeLogoMobile"
        size="cover"
        fixed={false}
      >
        <ColStyle>
          <ShopHeaderIcons />
        </ColStyle>

        <LanguagePicker className="mobile_languagePicker" />
      </BackgroundThemed>
      <div className="mobile__intro__info">
        <MobileShopIntro />
        <OrderMethod
          onChange={order => {
            if (order.usingDeliveryOrderMethod) {
              history.push('/delivery')
            } else if (order.usingPickupOrderMethod) {
              history.push('/pickup')
            }
          }}
        />
      </div>
    </StyledMobileIntroView>
  )
}
