import { Trans } from '@lingui/macro'
import { useObserver } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'
import { UAParser } from 'ua-parser-js'

import shareIcon from '../../app/icons/share-icon.svg'
import { useRoot } from '../../app/useRoot'

const StyledAppleBanner = styled.div`
  width: 100%;
  height: 5rem;
  background: #f4f4f4;
  padding: 1rem;
  position: absolute;
  bottom: 0rem;
  z-index: 50;
  opacity: 0.9;

  a {
    display: inline-block;
    vertical-align: middle;
    margin: 0 5px 0 0;
    font-family: ArialRoundedMTBold, Arial;
    font-size: 20px;
    text-align: center;
  }
`

const ShareIcon: React.FC = () => {
  return <img width="26" height="26" src={shareIcon} alt="Share" />
}

export const AppleBanner: React.FC = () => {
  const { user } = useRoot()

  return useObserver(() => {
    if (user.showAppleBanner && isIos() && !isInStandaloneMode()) {
      return (
        <StyledAppleBanner onClick={user.hideAppleBanner}>
          {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
          <a>&times;</a>
          <Trans>
            Install this webapp on your device: Tap <ShareIcon /> and then Add
            to homescreen.
          </Trans>
        </StyledAppleBanner>
      )
    }
    return null
  })
}

function isIos() {
  const agent = new UAParser(window.navigator.userAgent)
  const os = agent.getOS()
  return os.name === 'iOS'
}

function isInStandaloneMode() {
  // @ts-ignore standalone typing
  return 'standalone' in window.navigator && window.navigator.standalone
}
