import './MobileMap.css'

import { useRouterContext } from '@speedlo/hooks'
import React from 'react'

import { useRoot } from '../../app/useRoot'
import { BaseMap } from '../../common/BaseMap'
import { i18n } from '../../i18n'
import { DeliveryMap } from '../map/DeliveryMap'
import MobileHeader from './MobileHeader'

export const MobileDeliveryView: React.FC = () => {
  const { map } = useRoot()
  const { history } = useRouterContext()
  return (
    <div>
      <MobileHeader
        text={i18n.t`Enter the address`}
        buttonText={i18n.t`Back`}
        onButtonClick={() => history.goBack()}
      />
      <div className="mobile__map__view">
        <BaseMap model={map} isMobile={true}>
          <DeliveryMap onConfirm={() => history.push('/order')} />
        </BaseMap>
      </div>
    </div>
  )
}
