import './MobileSmartBanner.css'

import { Trans } from '@lingui/macro'
import { observer } from 'mobx-react-lite'
import React from 'react'
import styled from 'styled-components/macro'
import { UAParser } from 'ua-parser-js'

import { useRoot } from '../../app/useRoot'
import { MobilePlatformEnum, useQSmartBanner } from '../../graph/generated'
import { TBannerModel } from '../../models/BannerModel'

const SmartBannerContainer = styled.div`
  border-bottom: 1px solid #8f8f8f;
  height: 81px;
  position: absolute;
  z-index: 1;
  top: 3rem;
  width: 100%;
`

export const MobileSmartBanner = observer(() => {
  const { business } = useRoot()
  const platform = getPlatformType()

  let showBanner: Maybe<TBannerModel> = null
  if (business.isValid && platform !== null) {
    const banner = business.ensureBannerForCurrentBranch()
    if (banner.shouldBeVisible) {
      showBanner = banner
    }
  }

  const { data, loading } = useQSmartBanner(
    {
      branchId: business.branchId!,
      platform: platform!,
    },
    {
      skip: showBanner === null,
    },
  )

  if (showBanner === null || loading || !data.app) {
    return null
  }

  const { app } = data

  const wrapperClassName = `smartbanner smartbanner-ios smartbanner-top`

  return (
    <SmartBannerContainer>
      <div className={wrapperClassName}>
        <div className="smartbanner-container">
          {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
          <a className="smartbanner-close" onClick={showBanner.setClosedNow}>
            &times;
          </a>
          <span
            className="smartbanner-icon"
            style={{ backgroundImage: `url(${app.icon})` }}
          />
          <div className="smartbanner-info">
            <div className="smartbanner-title">{app.title}</div>
            <div className="smartbanner-description">
              <Trans>Free</Trans>
            </div>
          </div>
          <div className="smartbanner-wrapper">
            <a
              href={app.url}
              onClick={showBanner.setInstalledNow}
              className="smartbanner-button"
            >
              <span className="smartbanner-button-text">
                <Trans>Show app</Trans>
              </span>
            </a>
          </div>
        </div>
      </div>
    </SmartBannerContainer>
  )
})

export function getPlatformType(): Maybe<MobilePlatformEnum> {
  const agent = new UAParser(window.navigator.userAgent)
  const os = agent.getOS()
  if (os.name === 'iOS') {
    return MobilePlatformEnum.Ios
  } else if (os.name === 'Android') {
    return MobilePlatformEnum.Android
  }
  return null
}
