import React from 'react'
import styled from 'styled-components/macro'

import { useRoot } from '../../app/useRoot'
import { BaseMap } from '../../common/BaseMap'
import { PrimaryButton } from '../../common/Buttons'
import ButtonStripe from '../../common/ButtonStripe'
import { MapBranchMarker } from '../../common/MapBranchMarker'
import { FPickupBranchFragment } from '../../graph/generated'
import { i18n } from '../../i18n'
import { MobilePickupBranch } from './MobilePickupBranch'

interface IProps {
  branch: FPickupBranchFragment
  onSelect(): void
}

const StyledMobilePickupMap = styled.div`
  .branch {
    position: absolute;
    top: 3rem;
    opacity: 0.8;
    font-size: 0.8rem;
    width: 100%;

    .header {
      font-size: 1rem;
    }
  }

  .submit {
    position: fixed;
    bottom: 3rem;
    width: 100%;
    z-index: 10;
  }
`

export const MobilePickupMap: React.FC<IProps> = ({ branch, onSelect }) => {
  const { map } = useRoot()

  React.useEffect(() => {
    map.updatePosition(branch.gps)
  }, [branch, map])

  return (
    <StyledMobilePickupMap>
      <div className="mobile__map__view">
        <BaseMap model={map} isMobile={true}>
          <MapBranchMarker branch={branch} />
        </BaseMap>
      </div>
      <div className="branch">
        <MobilePickupBranch branch={branch} />
      </div>
      <div className="submit">
        <ButtonStripe>
          <PrimaryButton
            onClick={onSelect}
            disabled={!branch.pickupEnabled}
            data-testid="mobilePickupMap__SubmitButton"
          >
            {branch.pickupEnabled
              ? i18n.t`Choose branch`
              : i18n.t`Delivery only`}
          </PrimaryButton>
        </ButtonStripe>
      </div>
    </StyledMobilePickupMap>
  )
}
