import { idPropEq } from '@speedlo/tools'
import { observer } from 'mobx-react-lite'
import React from 'react'
import { RouteComponentProps } from 'react-router-dom'

import { useRoot } from '../../app/useRoot'
import Pacman from '../../common/Pacman'
import { FPickupBranchFragment } from '../../graph/generated'
import { i18n } from '../../i18n'
import { useOrderCreate } from '../map/hooks/useOrderCreate'
import { usePickupMapQuery } from '../map/hooks/usePickupMapQuery'
import MobileHeader from './MobileHeader'
import MobilePickupList from './MobilePickupList'
import { MobilePickupMap } from './MobilePickupMap'

type TBranch = FPickupBranchFragment

interface IRouteParams {
  branchId?: string
}

interface IProps extends RouteComponentProps<IRouteParams> {}

export const MobilePickupView = observer<IProps>(({ history, match }) => {
  const { business } = useRoot()

  const showBranchId = match.params.branchId

  const { data, loading } = usePickupMapQuery(showBranchId)
  const { createPickupOrder } = useOrderCreate(() => {
    history.push('/order')
  })

  const renderHeader = (branch: Maybe<TBranch>) => (
    <MobileHeader
      text={branch ? i18n.t`Pickup at branch` : i18n.t`Choose branch`}
      buttonText={i18n.t`Back`}
      onButtonClick={() => history.goBack()}
    />
  )
  const renderMap = (branch: TBranch) => (
    <MobilePickupMap
      branch={branch}
      onSelect={() => branch && createPickupOrder(branch.id)}
    />
  )
  const renderList = (branches: ReadonlyArray<TBranch>) => (
    <MobilePickupList
      branches={branches}
      onShowMap={({ id }) => history.push(`/pickup/${id}`)}
      onSelect={({ id }) => createPickupOrder(id)}
    />
  )

  if (loading) {
    return <Pacman />
  }

  if (business.isBrand && data.brand) {
    const branches = data.brand
    const branch =
      branches.length === 1
        ? branches[0]
        : (showBranchId && branches.find(idPropEq(showBranchId))) || null
    return (
      <>
        {renderHeader(branch)}
        {branch ? renderMap(branch) : renderList(branches)}
      </>
    )
  } else {
    const branch = data.branch
    return (
      <>
        {renderHeader(branch)}
        {renderMap(branch)}
      </>
    )
  }
})
