import React from 'react'

import { useQMobileShopStatus } from '../../graph/generated'
import { ShopClosingTimeModal } from '../shop/ShopClosingTimeModal'

interface IProps {
  branchId: ID
}

export const MobileClosingTimeModal: React.FC<IProps> = ({ branchId }) => {
  const { data, loading } = useQMobileShopStatus({
    branchId,
  })

  return loading ? null : <ShopClosingTimeModal status={data.branch} />
}
